export default [
  {
    name: 'layout',
    text: '容器布局',
    type: 'select',
    defaultValue: 'absolute',
    options: [
      { value: 'absolute', text: '绝对定位' },
      { value: 'relative', text: '流式布局' },
    ],
    onChange: (formState: any, v: string, { model }: any) => {
      if (!model.style) return v;
      if (v === 'relative') {
        model.style.height = 'auto';
      } else {
        model.displayMode = 'block';
      }
      const el = formState.stage?.renderer?.contentWindow.document.getElementById(model.id);
      if (el) {
        model.style.height = el.getBoundingClientRect().height;
      }
    },
  },
  {
    type: 'switch',
    name: 'isImageArea',
    text: '图片区域',
  },
  {
    name: 'displayMode',
    text: '布局模式',
    type: 'radio-group',
    display: (_: any, { model }: any) => model.layout === 'relative',
    defaultValue: 'block',
    options: [
      {
        value: 'block',
        text: '块布局block',
      },
      {
        value: 'flex',
        text: '弹性布局flex',
      },
    ],
    onChange: (formState: any, v: string, { model }: any) => {
      if (!model.style) return v;
      model.style.display = v;
    },
  },
  {
    name: 'flexDirection',
    text: '主轴方向',
    type: 'radio-group',
    display: (_: any, { model }: any) => model.displayMode === 'flex',
    defaultValue: 'row',
    options: [
      {
        value: 'row',
        text: '水平',
      },
      {
        value: 'column',
        text: '垂直',
      },
    ],
    onChange: (formState: any, v: string, { model }: any) => {
      if (!model.style) return v;
      model.style['flex-direction'] = v;
    },
  },
  {
    name: 'flexMainAxis',
    text: '主轴对齐',
    type: 'radio-group',
    display: (_: any, { model }: any) => model.displayMode === 'flex',
    options: [
      {
        value: 'flex-start',
        text: '左对齐',
      },
      {
        value: 'flex-end',
        text: '右对齐',
      },
      {
        value: 'center',
        text: '居中',
      },
      {
        value: 'space-between',
        text: '两端对齐',
      },
      {
        value: 'space-around',
        text: '横向平分',
      },
    ],
    onChange: (formState: any, v: string, { model }: any) => {
      if (!model.style) return v;
      model.style.justifyContent = v;
    },
  },
  {
    name: 'flexCrossAxis',
    text: '交叉轴对齐',
    type: 'radio-group',
    display: (_: any, { model }: any) => model.displayMode === 'flex',
    options: [
      {
        value: 'flex-start',
        text: '上对齐',
      },
      {
        value: 'flex-end',
        text: '下对齐',
      },
      {
        value: 'center',
        text: '居中',
      },
      {
        value: 'baseline',
        text: '基线对齐',
      },
      {
        value: 'stretch',
        text: '撑满容器',
      },
    ],
    onChange: (formState: any, v: string, { model }: any) => {
      if (!model.style) return v;
      model.style.alignItems = v;
    },
  },
  {
    name: 'flexWrap',
    text: '换行',
    type: 'radio-group',
    display: (_: any, { model }: any) => model.displayMode === 'flex',
    options: [
      {
        value: 'nowrap',
        text: '不换行',
      },
      {
        value: 'wrap',
        text: '换行',
      },
      {
        value: 'wrap-reverse',
        text: '逆序换行',
      },
    ],
    onChange: (formState: any, v: string, { model }: any) => {
      if (!model.style) return v;
      model.style.flexWrap = v;
      if (v !== 'nowrap') {
        model.style.alignContent = 'flex-start';
      }
    },
  },
];
